<template>
  <div class="main-container">
      <video
      v-for="item in items"
      v-bind:key="item.id"
        :id="forId(item.id)"
        v-bind:src="item.src"
        playsinline
        x5-playsinline
        webkit-playsinline
        x5-video-player-type="h5"
        x-webkit-airplay="true"
        airplay="allow"
        x5-video-can-play-with-audio="true"
        v-bind:class="{'video-container':item.isShow ,'video-hidden-container':!item.isShow }"
        x5-video-player-fullscreen="true"
        x5-video-orientation="portrait"
        preload
        :poster="item.postersrc"
        @canplaythrough="videoCanPlay(item.id)"
        @ended="onVideoEnded(item.id)"
      ></video>
    <VideoCanvas 
    v-bind:videoId="currentId" 
    v-bind:tipsrc="tipsrc" 
    v-bind:showTips="showTips" 
    v-bind:isAndroid="isAndroid" />
    <OperatorView
      v-bind:mode="mode"
      v-bind:videoId="currentId"
      v-bind:tipsrc="tipsrc"
      v-bind:isAndroid="isAndroid"
      v-on:swipeup="swipeup(currentId)"
      v-on:swipedown="swipedown(currentId)"
      v-on:tap="tap(currentId)"
      v-on:shake="shake(currentId)"
    />
  </div>
</template>

<script>
import video001 from "@/assets/video/video001.mp4";
import video002 from "@/assets/video/video002.mp4";
import video003 from "@/assets/video/video003.mp4";
import video005 from "@/assets/video/video005.mp4";
import image_tap from "@/assets/img/tap.png";
import image_swipeup from "@/assets/img/swipeup.png";
import image_swipedown from "@/assets/img/swipedown.png";
import image_shake from "@/assets/img/shake.png";
import image_like from "@/assets/img/like.png";
import image_dislike from "@/assets/img/dislike.png";
import image_loadingfinish from "@/assets/img/loadingfinish.png";

import { setTimeout } from "timers";
import OperatorView from "@/components/OperatorView.vue";
import VideoCanvas from "@/components/VideoCanvas.vue";
// v-bind:style="{ display:item.isShow?'flex':'none',zIndex:item.isShow?5:4 }"
 // v-bind:style="{display:showTips?'none':'block' }"
// v-on:click="playVideo(item.id)"
// x5-video-player-type="h5"

const MODE_TAP = 0;
const MODE_UP = 1;
const MODE_DOWN = 2;
const MODE_SHAKE = 3;
const MODE_LIKE = 4;
export default {
  name: "VideoPlayer",
  props: {},
  components: {
    OperatorView,
    VideoCanvas
  },
  data() {
    return {
      mode: MODE_TAP,
      swipesuccess: false,
      tipsrc: image_loadingfinish,
      currentId: 0,
      showTips: true,
      isAndroid: true,
      items: [
        {
          id: 0,
          src: video005,
          isShow: true,
          isDisable: false,
          mode: MODE_TAP,
          tipsrc: image_loadingfinish,
          postersrc: image_loadingfinish
        },
        {
          id: 1,
          src: video001,
          isShow: false,
          isDisable: true,
          mode: MODE_UP,
          tipsrc: image_swipeup,
          postersrc: image_swipeup
        },
        {
          id: 2,
          src: video002,
          isShow: false,
          isDisable: true,
          mode: MODE_TAP,
          tipsrc: image_shake,
          postersrc: image_shake
        },
        {
          id: 3,
          src: video003,
          isShow: false,
          isDisable: true,
          mode: MODE_TAP,
          tipsrc: image_like,
          postersrc: image_like
        }
      ]
    };
  },
  watch: {},
  created() {
    if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
      //判断iPhone|iPad|iPod|iOS
      this.isAndroid = false;
      console.log("isIOS");
    } else if (/(Android)/i.test(navigator.userAgent)) {
      //判断Android
      this.isAndroid = true;
      console.log("isAndroid");
    } else {
      //pc
      this.isAndroid = false;
      console.log("isPC");
    }
    this.initwx();
  },
  methods: {
    playVideo(id) {
      this.showTips = false;
      let vdo = document.getElementById("videoPlay" + id);
      vdo.play();
      console.log(id + "play");
      if(id-1>=0){
          this.items[id - 1].isDisable = true;
        }else{
          this.items[this.items.length-1].isDisable = true;
      }
      setTimeout(() => {
        if (id + 1 < this.items.length) {
          this.tipsrc = this.items[id + 1].tipsrc;
        } else {
          this.tipsrc = this.items[0].tipsrc;
        }
      }, 1000);
      
    },
    forId: function(index) {
      return "videoPlay" + index;
    },
    onVideoEnded(id) {
      setTimeout(()=>{
      this.items[id].isShow = false;

      },500)
      this.showTips = true;
      this.swipesuccess = false;
      if(this.isAndroid){
        let vdo = document.getElementById("videoPlay" + id);
        vdo.load();

      }
      let idindex = id+1;
      if (id + 1 >= this.items.length) {
        idindex = 0;
      }
      this.items[idindex].isDisable = false;
      this.items[idindex].isShow = true;
      this.mode = this.items[idindex].mode;
      this.tipsrc = this.items[idindex].tipsrc;
      this.currentId = this.items[idindex].id;
      console.log(id + "onended");
    },
    swipeup(id) {
      if (this.mode != MODE_UP) {
        return;
      }
      console.log("swipeup" + this.mode);
      this.swipesuccess = true;
      this.playVideo(id);
    },
    swipedown(id) {
      if (this.mode != MODE_DOWN) {
        return;
      }
      this.swipesuccess = true;
      console.log("swipedown" + this.mode);
      this.playVideo(id);
    },
    tap(id) {
      if (this.mode != MODE_TAP) {
        return;
      }
      this.mode = MODE_UP;
      console.log("tap" + this.mode);
      this.playVideo(id);
    },
    shake(id) {
      if (this.mode != MODE_SHAKE) {
        return;
      }
      console.log("shake" + this.mode);
      this.playVideo(id);
    },
    onMove(event) {
      event.preventDefault();
      if (this.swipesuccess) {
        // this.playVideo(id);
        console.log("onMove" + this.mode);
      }
    },
    videoCanPlay(id) {
      console.log("videoCanPlay " + id);
    },
    initwx() {
      document.addEventListener("WeixinJSBridgeReady", () => {
        console.log("WeixinJSBridgeReady");
        for (let item of this.items) {
          this.loadVideo(item.id);
        }
      });
    },
    loadVideo(id) {
      let vdo = document.getElementById("videoPlay" + id);
      vdo.load();
    }
  }
};
</script>

<style scoped>
@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes fadeout /*Safari and Chrome*/ {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.main-container {
  align-items: center;
  justify-content: center;
}
.video-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 6;
  object-fit: fill;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  background-size: 100% 100% !important;
  background-color: rgb(0, 0, 0);
  transform-origin: 0% 0% 0px;
  justify-content: center;
}
.disable{
  display: none;
}
.video-hidden-container {
  position: absolute;
  display: block;
  align-items: center;
   width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 4;
  object-fit: fill;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  background-size: 100% 100% !important;
  justify-content: center;
}
.tipsimg {
  position: absolute;
  display: block;
  align-items: center;
  width: 200px;
  height: 200px;
  opacity: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 6;
}
.touch-view {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 7;
  /* background-color: green; */
  justify-content: center;
}
</style>
