<template>
  <div class="main-container">
    <div class="bg" />
    <img :src="bg" class="bg" />
    <img :src="bgframe" class="bg" />
    <!-- <img :src="posterSrc2" id="posterbg2" width="0" height="0" />
    <img :src="posterSrc3" id="posterbg3" width="0" height="0" />-->
    <swiper :options="swiperOption" class="swiper-container" ref="mySwiper">
      <swiper-slide class="swiper-item" v-for="item in items" v-bind:key="item.id">
        <div class="item-container" :id="'poster'+item.id">
          <img :src="item.src" class="img-item" />
          <p class="text-item">用户名{{item.id}}</p>
        </div>
      </swiper-slide>
    </swiper>
    <img :src="savesrc" class="saveimage" id="savedimage" />
    <img :src="playagain" class="playbutton" />
    <img :src="saveposter" class="savebutton"/>
    <button  class="playbutton" style="opacity:0" v-on:click="gotToPlayAgain()"/>
    <img :src="postertitle" class="postertitle" />
    <transition name="fade">
    <img v-if="showswipeup" :src="imageswipeup" class="imageswipeup"/>
  </transition>
  </div>
</template>


<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
import html2canvas from "html2canvas";
import image_posterbg from "@/assets/img/posterbg.png";
import image_playagain from "@/assets/img/playagain.png";
import image_posterframe from "@/assets/img/posterframe.png";
import image_postertitle from "@/assets/img/postertitle.png";
import image_saveposter from "@/assets/img/saveposter.png";
import image_swipeup from "@/assets/img/swipeuptips.png";
import image_poster1 from "@/assets/img/poster1.jpg";
import image_poster2 from "@/assets/img/poster2.jpg";
import image_poster3 from "@/assets/img/poster3.jpg";
import { setTimeout } from 'timers';
export default {
  name: "PosterCanvas",
  components: { swiper, swiperSlide },
  props: {
    videoId: Number,
    isAndroid: Boolean,
    tipsrc: String,
    showTips: Boolean
  },
  data() {
    return {
      savesrc: image_poster1,
      bg: image_posterbg,
      bgframe: image_posterframe,
      saveposter: image_saveposter,
      postertitle:image_postertitle,
      playagain: image_playagain,
      imageswipeup:image_swipeup,
      showswipeup:true,
      items: [
        {
          id: 0,
          src: image_poster1,
          afterSrc: null
        },
        {
          id: 1,
          src: image_poster2,
          afterSrc: null
        },
        {
          id: 2,
          src: image_poster3,
          afterSrc: null
        }
      ],
      swiperOption: {
        direction: "vertical",
        observer: true,
        observeParents: true,
        pagination: {
          clickable: true
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  watch: {},
  mounted() {
    this.swiper.on("slideChangeTransitionEnd", () => {
      this.savePoster(this.swiper.activeIndex);
      this.showswipeup = false;
      console.log("slideChangeTransitionEnd");
    });
  },
  created() {},
  methods: {
    savePoster(id) {
      
      //这里的0.7是随swiper-container的宽度决定的
      let width = document.documentElement.clientWidth * 0.65;
      let height = width / 1080 * 1920;
      let canvas = document.createElement("canvas");
      canvas.width = width * 2;
      canvas.height = height * 2;
      canvas.style.width = width + "px";
      canvas.style.height = height + "px";
      let context = canvas.getContext("2d");
      context.scale(2, 2);
      let origin = document.getElementById("poster" + id);
      let target = document.getElementById("savedimage");
      html2canvas(origin, {
        width: width,
        height: height,
        canvas: canvas
      }).then(function(canvas) {
        let src = canvas.toDataURL("image/png");
        // console.log(src);
        target.src = src;
      });
    },
    gotToPlayAgain() {
      console.log("reload");
      window.location.reload();
    }
  }
};
</script>

