<template>
  <div class="main-container">
    <img :src="tipsrc" id="posterbg" width="1" height="1" />
    <canvas class="video-container" id="videocanvas"  />
  </div>
</template>


<script>
import image_loadingfinish from "@/assets/img/loadingfinish.png";
import image_tap from "@/assets/img/tap.png";
import image_swipeup from "@/assets/img/swipeup.png";
import image_swipedown from "@/assets/img/swipedown.png";
import image_shake from "@/assets/img/shake.png";
import image_like from "@/assets/img/like.png";
import image_dislike from "@/assets/img/dislike.png";
var canvas;
var ctx;
var requestAnimationFrame;
var cancelAnimationFrame;
var timer;
export default {
  name: "VideoCanvas",
  props: {
    videoId: Number,
    isAndroid: Boolean,
    tipsrc: String,
    showTips:Boolean
  },
  data() {
    return {
      posterSrc: image_loadingfinish,
      isOpenVideoDraw:false
    };
  },
  watch: {},
  mounted() {
    canvas = document.getElementById("videocanvas");
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    console.log(canvas.width);
    console.log(canvas.height);
    ctx = canvas.getContext("2d");
    requestAnimationFrame =
      window.requestAnimationFrame ||
      window.mozRequestAnimationFrame ||
      window.webkitRequestAnimationFrame ||
      window.msRequestAnimationFrame;
    cancelAnimationFrame =
      window.cancelAnimationFrame || window.mozCancelAnimationFrame;
      if(this.isAndroid){
        // this.drawPoster();
      }else{
        this.drawCanvas();
      }
  },
  created() {
  },
  methods: {
    drawPoster(){
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      let video = document.getElementById("videoPlay" + this.videoId);
      if (video.paused && this.showTips){
        let img = document.getElementById("posterbg");
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
      }else{

      }
      timer = requestAnimationFrame(this.drawPoster);
    },
    drawCanvas() {
      let video = document.getElementById("videoPlay" + this.videoId);
      // let offsetX = 0;
      // let offsetY = 0;
      // let angle = 0;
      // if (!this.isAndroid&&this.isOpenVideoDraw) {
      //   ctx.translate(offsetX, offsetY);
      //   ctx.rotate((Math.PI / 180) * angle);
      //   ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
      //   ctx.rotate((-Math.PI / 180) * angle);
      //   ctx.translate(-offsetX, -offsetY);
      // }
      let isBlack = false;
      let isWhite = false;
      let pixel1 = ctx.getImageData(5, 5, 1, 1);
        let pixel2 = ctx.getImageData(50, 50, 1, 1);
        let pixel3 = ctx.getImageData(100, 100, 1, 1);
        isBlack =
          pixel1.data[0] == 0 &&
          pixel1.data[1] == 0 &&
          pixel1.data[2] == 0 &&
          pixel2.data[0] == 0 &&
          pixel2.data[1] == 0 &&
          pixel2.data[2] == 0 &&
          pixel3.data[0] == 0 &&
          pixel3.data[1] == 0 &&
          pixel3.data[2] == 0;
        isWhite =
          pixel1.data[0] == 255 &&
          pixel1.data[1] == 255 &&
          pixel1.data[2] == 255 &&
          pixel2.data[0] == 255 &&
          pixel2.data[1] == 255 &&
          pixel2.data[2] == 255 &&
          pixel3.data[0] == 255 &&
          pixel3.data[1] == 255 &&
          pixel3.data[2] == 255;
      // console.log('video:'+video.paused+' black' + isBlack);
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      if (video.paused) {
        let img = document.getElementById("posterbg");
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
      }
      timer = requestAnimationFrame(this.drawCanvas);
    },
    stopDrawing() {
      cancelAnimationFrame(timer);
    },
    isInvildColor(){
      let isBlack = false;
      let isWhite = false;
      if(!ctx){
          return true;
        }
      let pixel1 = ctx.getImageData(5, 5, 1, 1);
        let pixel2 = ctx.getImageData(50, 50, 1, 1);
        let pixel3 = ctx.getImageData(100, 100, 1, 1);
        if(!pixel1){
          return true;
        }
        isBlack =
          pixel1.data[0] == 0 &&
          pixel1.data[1] == 0 &&
          pixel1.data[2] == 0 &&
          pixel2.data[0] == 0 &&
          pixel2.data[1] == 0 &&
          pixel2.data[2] == 0 &&
          pixel3.data[0] == 0 &&
          pixel3.data[1] == 0 &&
          pixel3.data[2] == 0;
        isWhite =
          pixel1.data[0] == 255 &&
          pixel1.data[1] == 255 &&
          pixel1.data[2] == 255 &&
          pixel2.data[0] == 255 &&
          pixel2.data[1] == 255 &&
          pixel2.data[2] == 255 &&
          pixel3.data[0] == 255 &&
          pixel3.data[1] == 255 &&
          pixel3.data[2] == 255;
          return isBlack||isWhite;
    }
  }
};
</script>

<style scoped>
.main-container {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 7;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
}
.video-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 7;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 1;
  z-index: 7;
  justify-content: center;
}
</style>
