<template>
  <div class="home" id="main">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <div v-bind:style="{ opacity:isFinished?1:0 }"><PosterCanvas ref="poster"/></div>
    <div id="videos" v-bind:style="{ opacity:isLoading||isFinished?0:1 }"><VideoPlayerNew v-on:playFinish="playFinish"/></div>
    <TypingEffect v-if="isLoading" id="typing" v-on:loadFinish="loadFinish" v-bind:style="{ opacity:isLoading?1:0 }" />
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import TypingEffect from '@/components/TypingEffect.vue'
import VideoPlayer from '@/components/VideoPlayer.vue'
import VideoPlayerNew from '@/components/VideoPlayerNew.vue'
import PosterCanvas from "@/components/PosterCanvas.vue"
import { setTimeout } from 'timers';
export default {
  name: 'home',
  data() {
    return {
      isLoading:true,
      isFinished:false
    };
  },
  components: {
    HelloWorld,
    TypingEffect,
    VideoPlayer,
    VideoPlayerNew,
    PosterCanvas
  },
 
  methods:{
    loadFinish(){
      this.isLoading = false;
      let typing = document.getElementById("typing");
      let main = document.getElementById("main");
      main.removeChild(typing);
    },
    playFinish(){
      this.isFinished = true;
      let videos = document.getElementById("videos");
      let main = document.getElementById("main");
      main.removeChild(videos);
      setTimeout(()=>{this.$refs.poster.savePoster(0);},100)
      
    }
  }
}
</script>
<style lang="scss">
.home {
  position: fixed;
  display: block;
  align-items: center;
  width: 1080px;
  height: 1875px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  padding: 0px;
  margin: 0px;
  overflow: hidden;
  background: url('~@/assets/img/loadingbg.png');
  justify-content: center;
}
</style>