<template>
  <div class="main-container" id="main">
    <div class="main-container" id="second">
      <video
        v-on:click="playVideo()"
        id="videoPlay"
        v-bind:src="videosrc"
        playsinline
        x5-playsinline
        webkit-playsinline
        x5-video-player-type="h5"
        x5-video-can-play-with-audio="true"
        class="video-container"
        x5-video-player-fullscreen="true"
        x5-video-orientation="portrait"
        preload
        v-bind:style="{ display:isFinished?'none':'block' }"
        :poster="postersrc"
        @canplaythrough="videoCanPlay()"
        @ended="onVideoEnded()"
        @timeupdate="videoUpdate()"
      ></video>
      <OperatorViewNew
      v-bind:mode="mode"
      v-bind:isAndroid="isAndroid"
      v-bind:isFinished="isFinished"
      v-on:swipeup="swipeup()"
      v-on:swipedown="swipedown()"
      v-on:tap="tap()"
      v-on:shake="shake()"
      v-bind:style="{ display:isFinished?'none':'block' }"
    />
    </div>
  </div>
</template>

<script>
import video001 from "@/assets/video/video001.mp4";
import video002 from "@/assets/video/video002.mp4";
import video003 from "@/assets/video/video003.mp4";
import video005 from "@/assets/video/video005.mp4";
import video007 from "@/assets/video/video007.mp4";
import image_tap from "@/assets/img/tap.png";
import image_swipeup from "@/assets/img/swipeup.png";
import image_swipedown from "@/assets/img/swipedown.png";
import image_shake from "@/assets/img/shake.png";
import image_like from "@/assets/img/like.png";
import image_dislike from "@/assets/img/dislike.png";
import image_loadingfinish from "@/assets/img/loadingfinish.png";
import image_startplay from "@/assets/img/startplay.jpg";
import OperatorViewNew from "@/components/OperatorViewNew.vue";
import { setTimeout } from "timers";
// v-bind:style="{ display:item.isShow?'flex':'none',zIndex:item.isShow?5:4 }"
// v-on:click="playVideo(item.id)"
// x5-video-player-type="h5"
const MODE_TAP = 0;
const MODE_UP = 1;
const MODE_DOWN = 2;
const MODE_SHAKE = 3;
const MODE_LIKE = 4;
export default {
  name: "VideoPlayerNew",
  props: {},
  components: {OperatorViewNew},
  data() {
    return {
      videosrc:video007,
      postersrc:image_startplay,
      isAndroid:true,
      isFinished:false,
      mode:MODE_UP,
      loopup:{startTime:36,endTime:39,mode:MODE_UP,isDone:false},
      loopshake:{startTime:71,endTime:72,mode:MODE_TAP,isDone:false},
      jumpEnd:false
    };
  },
  watch: {},
  created() {
    this.initwx();
    if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
      //判断iPhone|iPad|iPod|iOS
      this.isAndroid = false;
      console.log("isIOS");
    } else if (/(Android)/i.test(navigator.userAgent)) {
      //判断Android
      this.isAndroid = true;
      console.log("isAndroid");
    } else {
      //pc
      this.isAndroid = false;
      console.log("isPC");
    }
    this.initwx();
  },
  methods: {
    playVideo() {
      if(this.isFinished){
        return;
      }
      let vdo = document.getElementById("videoPlay");
      vdo.play();
      console.log("play");
    },
    onVideoEnded() {
      console.log("onended");
      this.mode = this.loopup.mode;
      this.loopup.isDone = false;
      this.loopshake.isDone = false;
      this.isFinished = true;
      this.$emit("playFinish");
    },
    onMove(event) {
      if(!this.isFinished){
        event.preventDefault();
        console.log('preventDefault');
      }
    },
    videoCanPlay() {
      console.log("videoCanPlay");
    },
    initwx() {
      document.addEventListener("WeixinJSBridgeReady", () => {
        console.log("WeixinJSBridgeReady");
        this.loadVideo();
      });
    },
    loadVideo() {
      let vdo = document.getElementById("videoPlay");
      vdo.load();
    },
    changeVideoTime(vdo,loop){
      if(vdo.currentTime > loop.endTime&&loop.mode==this.mode&&!loop.isDone&&!this.isFinished){
          vdo.currentTime = loop.startTime;
          vdo.muted = true;
      }
    },
    doOperator(loop){
      let vdo = document.getElementById("videoPlay");
      if(vdo.currentTime>=loop.startTime&&vdo.currentTime<=loop.endTime){
        loop.isDone = true;
        vdo.currentTime = loop.endTime;
        vdo.muted = false;
        return true;
      }
      return false;
    },
    videoUpdate(){
      let vdo = document.getElementById("videoPlay");
      if(!this.jumpEnd){
        this.jumpEnd = true;
        vdo.currentTime = 8.5;
        vdo.play();
      }
      this.changeVideoTime(vdo,this.loopup);
      this.changeVideoTime(vdo,this.loopshake);
      
    },
    swipeup(id) {
      if (this.mode != MODE_UP) {
        return;
      }
      console.log("swipeup" + this.mode);
      if(this.doOperator(this.loopup)){
        setTimeout(()=>{this.mode = this.loopshake.mode},1000);
      }
      
    },
    swipedown(id) {
      if (this.mode != MODE_DOWN) {
        return;
      }
    },
    tap(id) {
      this.doOperator(this.loopshake);
      
    },
    shake() {
      if (this.mode != MODE_SHAKE) {
        return;
      }
      this.doOperator(this.loopshake);
    },
  }
};
</script>

<style scoped>
.main-container {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  opacity: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  align-items: center;
  justify-content: center;
}
.video-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 6;
  object-fit: fill;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  background-size: 100% 100% !important;
  transform-origin: 0% 0% 0px;
  justify-content: center;
}
.video-hidden-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: auto;
  opacity: 1;
  z-index: 5;
  justify-content: center;
  overflow: hidden;
}
</style>
