<template>
  <div class="main-container">
    <div
      class="touch-view"
      @touchcancel="onCancel"
      @touchstart="onStart"
      @touchend="onEnd"
      @touchmove="onMove"
    >
    </div>
  </div>
</template>


<script>
import { setTimeout } from "timers";
import Shake from "shake.js";
const MODE_TAP = 0;
const MODE_UP = 1;
const MODE_DOWN = 2;
const MODE_SHAKE = 3;
const MODE_LIKE = 4;
export default {
  name: "OperatorView",
  props: {
    mode: Number,
    isAndroid: Boolean,
    isFinished:Boolean
  },
  data() {
    return {
      startX: 0,
      startY: 0,
      isMovingUp: false,
      isMovingDown: false
    };
  },
  watch: {},
  mounted() {
    // 实例化一个 shake 对象
    let myShakeEvent = new Shake({
      threshold: 20, // 默认摇动阈值
      timeout: 1200 // 默认两次事件间隔时间
    });
    // 监听设备的动作
    myShakeEvent.start();
    // 添加一个事件监听
    window.addEventListener("shake", this.shakeEventDidOccur, false);
  },
  created() {},
  methods: {
    removeTouchView(){
    },
    onMove(event) {
      if(this.isFinished){
        return false;
      }
      console.log('preventDefault');
      event.preventDefault();
      let y = event.touches[0].clientY;
      if (y + 100 < this.startY && this.mode == MODE_UP) {
        this.isMovingUp = true;
        this.startY = 0;
      }
      if (y - 100 > this.startY && this.mode == MODE_DOWN) {
        this.isMovingDown = true;
        this.startY = 0;
      }
      return false;
    },
    onCancel(event) {
      if(this.isFinished){
        return false;
      }
      this.startX = 0;
      this.startY = 0;
    },
    onStart(event) {
      if(this.isFinished){
        return false;
      }
      this.isMovingUp = false;
      this.isMovingDown = false;
      this.startX = event.touches[0].clientX;
      this.startY = event.touches[0].clientY;
      if (MODE_TAP != this.mode && !this.isAndroid) {
        // let vdo = document.getElementById("videoPlay");
        // if (vdo.paused) {
        //   vdo.play();
        //   vdo.pause();
        //   console.log("onStart video pause");
        // }
      }
    },
    onEnd(event) {
      if(this.isFinished){
        return false;
      }
      console.log("touch onEnd" + this.mode+this.isMovingUp);
      let vdo = document.getElementById("videoPlay");
      vdo.play();
      if (this.mode == MODE_TAP) {
        console.log("tap");
        this.$emit("tap");
      } else if (this.mode == MODE_UP && this.isMovingUp) {
        console.log("swipeupDo");
        this.$emit("swipeup");
      } else if (this.mode == MODE_DOWN && this.isMovingDown) {
        console.log("swipedownDo");
        this.$emit("swipedown");
      }
      this.isMovingUp = false;
      this.isMovingDown = false;
      this.startX = 0;
      this.startY = 0;
    },
    getDistance(x1, y1, x2, y2) {
      let x = x1 - x2;
      let y = y1 - y2;
      return Math.sqrt(x * x + y * y);
    },
    // 摇动的回调函数
    shakeEventDidOccur() {
      if (this.mode == MODE_SHAKE) {
        console.log("shake");
        this.$emit("shake");
      }
      // alert('shake')
      if (window.navigator.vibrate) {
        navigator.vibrate(500);
      }
    }
  }
};
</script>

<style scoped>
.main-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  justify-content: center;
}
.tipsimg {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: 0;
  opacity: 1;
  z-index: 10;
}
.tipsimg-gif-loading {
  position: absolute;
  display: block;
  align-items: center;
  width: 738px;
  height: 147px;
  left: 0;
  right: 0;
  top: 10%;
  bottom: 0;
  margin: auto;
  opacity: 1;
  z-index: 7;
}
.touch-view {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 7;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  /* background-color: green; */
  justify-content: center;
}
</style>
