<template>
  <div id = "typeeffectdiv"  class="main-container">
    <img class="bg" src="~@/assets/img/loadingbg.png"/>
    <div class="loading-container" >
      <div style="margin: 30px;height:40%;"><p id="typewords" class="word-text" >{{word}}</p></div>
      <!-- <p id="loadingtext" class="progress-text">{{progressText}}</p> -->
      <img :src="gifSrc"  class="tipsimg-gif-loading" />
    </div>
    <!-- <img id="gifimg" :src="gifSrc"  class="tipsimg-gif-loadingpage" />  -->
  </div>
  
</template>


<script>
import gif_textloading from "@/assets/img/textloading.gif";
// import gif_loadingpage from "@/assets/img/loadingpage.gif";
import { setTimeout } from 'timers';

export default {
  name: "TypingEffect",
  props: {
    msg: String
  },
  data() {
    return {
      gifSrc:gif_textloading,
      words: `距今9000－7500年，来自远古的贾湖部落先民就已经开始酿造葡萄酒。《诗经》所反映的3600年前的殷商时代，就已经有了关于葡萄的文字记载......`,
      word: "111",
      index: 0,
      progressText: "",
      percent: 0
    };
  },
  watch: {},
  created() {
  },
  mounted(){
    this.setLoadingDisplay();
  },
  methods: {
    setLoadingDisplay() {
      let v1 = setInterval(() => {
        this.word = this.words.substring(0, this.index++)+'_';
        this.index++;
        if (this.word.length >= this.words.length-1) {
          this.word = this.words.substring(0,this.words.length);
          clearInterval(v1);
          setTimeout(()=>{this.callFinish()},1000)
        }
      }, 100);
      let v2 = setInterval(() => {
        this.percent++;
        this.progressText = "";
        // this.progressText = "loading " + this.percent.toFixed(0) + "%";
        if (this.percent >= 100) {
          clearInterval(v2);
          // this.progressText = "载入完成";
          
        }
      }, 10);
    },
    callFinish() {
      console.log("callFinish");
        this.$emit("loadFinish");
      if (this.percent >= 100) {
        // this.$emit("loadFinish");
        console.log("loadFinish");
      }
    },
  }
}
</script>


<style scoped>
@font-face{
  font-family: LoadingFont;
  src: url("../assets/loading.ttf");
}
/*光标动画*/
@keyframes caret {
  50% {
    border-color: transparent;
  }
}

/*文本动画：从宽度为0开始*/
@keyframes text {
  from {
    width: 0;
  }
}
.main-container {
  position: absolute;
  display: block;
  align-items: center;
  width: 1080px;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  justify-content: center;
}
.bg{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.loading-container {
  position: relative;
  display: block;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.word-text {
  position: absolute;
  display: block;
  align-items: center;
  left: 15%;
  right: 15%;
  top: 19%;
  font-size: 52px;
  font-family: "LoadingFont";
  font-weight: bold;
  color: #000;
  line-height: 2;
  align-content: center;
  text-align: left;
}
.progress-text {
  display: block;
  position: relative;
  align-items: center;
  margin-top: 100px;
  text-align: center;
  font-size: 60px;
}
.tipsimg-gif-loading {
  position: absolute;
  display: block;
  align-items: center;
  width: 738px;
  height: 147px;
  left: 0;
  right: 0;
  top: 8%;
  bottom: 0;
  margin: auto;
  opacity: 1;
  z-index: 7;
}

.tipsimg-gif-loadingpage {
  position: absolute;
  display: block;
  align-items: center;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  opacity: 1;
  z-index: 7;
}
</style>
